import store from "../../store";
import { RouterMiddleware } from "..";
import { SKIP_USER_AUTHENTICATION, DEVELOPMENT_CREDENTIALS } from "@/constants";
import { authModule } from "@/store/modules";

/**
 * The user is not allowed to visit this page if they are not authenticated
 * @param {*} param0
 */
export const isAuthenticated: RouterMiddleware = async function({ next, to }) {
  const isLoggedIn = store.getters["auth/isLoggedIn"];
  if (isLoggedIn) {
    authModule.setLoaded(true);
    return next(); // continue to the route
  }
  if (SKIP_USER_AUTHENTICATION) {
    try {
      await store.dispatch("auth/authenticate", DEVELOPMENT_CREDENTIALS);
      authModule.setLoaded(true);
      return next();
    } catch (err) {
      alert(`Automatic login failed: ${err.message || err}`);
    }
  }
  return next({
    path: "/login", // redirect to /login if user is not logged in
    query: {
      redirect: to.fullPath // redirect to this path after user has logged in
    }
  });
};

export const isAuthenticatedAdmin: RouterMiddleware = async function({ next, to }) {
  const isLoggedIn = store.getters["auth/adminIsLoggedIn"];
  if (isLoggedIn) {
    authModule.setLoaded(true);
    return next(); // continue to the route
  }
  return next({
    path: "/adminLogin", // redirect to /login if user is not logged in
    query: {
      redirect: to.fullPath // redirect to this path after user has logged in
    }
  });
};
