import * as api from "@/store/api";
import store from "@/store";
import { Module, VuexModule, Mutation, Action } from "vuex-module-decorators";
import { authModule, settingsModule } from ".";
import {
  ReportLanguage,
  Language,
  UpdateCheckInterval,
  EmailNotification,
  IUpdateUserSettingsResponseSuccess,
  IUpdateUserResponseSuccess,
  IUpdateGlobalSettingsResponseSuccess,
  IUserResponseSuccess,
  IServerResponse
} from "@/shared";
import throttle from "lodash.throttle";

export interface IThemeSettings {
  primary: string;
  secondary: string;
  accent: string;
  dark: boolean;
}

export interface IEmailSettings {
  sendEmails: boolean;
  hostname: string;
  port: number;
  username: string;
  password: string;
  from: string;
  recipient: string[];
  reportLanguage: ReportLanguage;
}

export interface IUserSettings {
  firstName: string;
  lastName: string;
  username: string;
  email: string;
  // password not saved here
}
export interface IGuiSettings {
  language: Language;
  showSnackbar: boolean;
  theme: IThemeSettings;
}
export interface IGeneralSettings {
  enableRegister: boolean;
  automaticUpdates: boolean;
  updateCheckInterval: UpdateCheckInterval;
  port: number;
}

export interface IAllSettings {
  notification: IEmailSettings;
  userData: IUserSettings;
  gui: IGuiSettings;
  general: IGeneralSettings;
  backup: IBackupSettings;
}

export interface IBackupSettings {
  defaultUnlockTime: number;
  defaultMaxBackupsPerRepo: number;
  defaultEmailNotification: EmailNotification;
}

@Module({ dynamic: true, store, name: "settings", namespaced: true })
export default class SettingsModule extends VuexModule {
  gui = {
    language: Language.en,
    showSnackbar: true,
    theme: {
      dark: true,
      primary: "#456CB2",
      secondary: "#424242",
      accent: "#82B1FF"
    }
  };
  userData = {
    email: "",
    firstName: "",
    lastName: "",
    username: ""
  };
  notification = {
    from: "",
    hostname: "",
    password: "",
    port: 0,
    username: "",
    recipient: [] as string[],
    reportLanguage: ReportLanguage.gui,
    sendEmails: true
  };
  general = {
    automaticUpdates: true,
    enableRegister: false,
    updateCheckInterval: UpdateCheckInterval.daily,
    port: 8380
  };
  backup = {
    defaultEmailNotification: EmailNotification.always,
    defaultMaxBackupsPerRepo: 999,
    defaultUnlockTime: 60 * 5 // 5 hours (time in minutes)
  };
  shouldUpdate = true; // for settings responses
  private serviceUpdateAvailable = false;

  @Mutation
  setShouldUpdate(u: boolean) {
    this.shouldUpdate = u;
  }

  @Mutation
  setUpdateAvailable(available: boolean) {
    this.serviceUpdateAvailable = available;
  }

  get updateAvailable() {
    return this.serviceUpdateAvailable;
  }

  /**
   * Warning: updates when any setting updates
   *
   * @readonly
   * @memberof SettingsModule
   */
  get allSettings(): IAllSettings {
    return {
      backup: this.backup,
      general: this.general,
      notification: this.notification,
      userData: this.userData,
      gui: this.gui
    };
  }

  @Action({ rawError: true })
  async testSmtpSettings({ subject, text }: { subject: string; text: string }) {
    const response = await api.testSmtpSettings(authModule.userId, {
      subject,
      text
    });
    return response;
  }

  @Action({})
  async getServiceUpdateAvailable() {
    const response = await api.getServiceUpdateAvailable();
    this.setUpdateAvailable(response.available);
  }

  @Action({})
  async updatePassword(password: string) {
    api.saveUserSettings(authModule.userId, {
      password
    });
  }

  @Action({ rawError: true })
  async updateService() {
    const response = await api.updateService();
    authModule.setLoaded(false);
    authModule.setServiceUpdating({ updating: true });
    return response;
  }

  @Action({ rawError: true })
  async fetchAllSettings() {
    const response1 = await api.getSettings(authModule.userId);
    const response2 = await api.getUserSettings(authModule.userId);
    const response3 = await api.getGlobalSettings(authModule.userId);
    if (response1.payload && response2.payload && response3.payload) {
      this.setSettings({
        settings: response1.payload,
        user: response2.payload,
        global: response3.payload
      });
    }
  }

  @Action({ rawError: true })
  async saveAllSettings() {
    await saveSettings();
  }

  @Action({ rawError: true })
  async saveUserSettings() {
    return await saveSettingsImmediately(false);
  }

  @Mutation
  setSettings({
    settings: { settings },
    user,
    global
  }: {
    settings: IUpdateUserSettingsResponseSuccess;
    user: IUserResponseSuccess | undefined;
    global: IUpdateGlobalSettingsResponseSuccess | undefined;
  }) {
    if (!this.shouldUpdate) return;
    this.gui.language = settings.language || Language.en;
    this.gui.showSnackbar = settings.showSnackbar || false;
    this.gui.theme.primary = settings.themePrimary || "#456CB2";
    this.gui.theme.accent = settings.themeAccent || "#82B1FF";
    this.gui.theme.dark = settings.darktheme || false;
    this.gui.theme.secondary = settings.themeSecondary || "#424242";

    this.notification.sendEmails = settings.sendEmails || false;
    this.notification.reportLanguage =
      settings.reportLanguage || ReportLanguage.gui;
    this.notification.from = settings.smtpFrom || "";
    this.notification.hostname = settings.smtpHostname || "";
    this.notification.password = settings.smtpPassword || "";
    this.notification.port = settings.smtpPort || 0;
    this.notification.username = settings.smtpUsername || "";
    this.notification.recipient =
      (settings.smtpTo && settings.smtpTo.split(";")) || [];

    this.backup.defaultEmailNotification =
      settings.defaultEmailNotification || EmailNotification.onerror;
    this.backup.defaultMaxBackupsPerRepo =
      settings.defaultMaxBackupsPerRepo || 100;
    this.backup.defaultUnlockTime = settings.defaultUnlockTime || 60; // time in minutes

    if (user) {
      this.userData.username = user.user.username;
      this.userData.email = user.user.email;
      this.userData.firstName = user.user.firstName;
      this.userData.lastName = user.user.lastName;
    }

    if (global) {
      this.general.automaticUpdates = global.settings.automaticUpdates;
      this.general.enableRegister = global.settings.enableRegister;
      this.general.updateCheckInterval = global.settings.updateCheckInterval;
      this.general.port = global.settings.port;
    }
  }

  @Mutation
  setDark(isDark: boolean) {
    this.gui.theme.dark = isDark;
  }

  @Mutation
  setLanguage(locale: Language) {
    this.gui.language = locale;
  }
  @Mutation
  setTheme(theme: { primary: string; accent: string; secondary: string }) {
    this.gui.theme = { ...theme, dark: this.gui.theme.dark };
  }
  @Mutation
  setShowSnackbar(show: boolean) {
    this.gui.showSnackbar = show;
  }
  @Mutation
  setAutomaticUpdates(enable: boolean) {
    this.general.automaticUpdates = enable;
  }
  @Mutation
  setEnableRegister(enable: boolean) {
    this.general.enableRegister = enable;
  }
  @Mutation
  setUpdateCheckInterval(interval: UpdateCheckInterval) {
    this.general.updateCheckInterval = interval;
  }
  @Mutation
  setUsername(name: string) {
    this.userData.username = name;
  }
  @Mutation
  setFirstName(name: string) {
    this.userData.firstName = name;
  }
  @Mutation
  setLastName(name: string) {
    this.userData.lastName = name;
  }
  @Mutation
  setEmail(email: string) {
    this.userData.email = email;
  }
  @Mutation
  setDefaultUnlockTime(time: number) {
    this.backup.defaultUnlockTime = time;
  }
  @Mutation
  setDefaultMaxBackupsPerRepo(max: number) {
    this.backup.defaultMaxBackupsPerRepo = max;
  }
  @Mutation
  setDefaultEmailNotification(type: EmailNotification) {
    this.backup.defaultEmailNotification = type;
  }
  @Mutation
  setSendEmails(send: boolean) {
    this.notification.sendEmails = send;
  }
  @Mutation
  setSmtpHostname(name: string) {
    this.notification.hostname = name;
  }
  @Mutation
  setSmtpPort(port: number) {
    this.notification.port = port;
  }
  @Mutation
  setReportLanguage(lang: ReportLanguage) {
    this.notification.reportLanguage = lang;
  }
  @Mutation
  setSmtpFrom(from: string) {
    this.notification.from = from;
  }
  @Mutation
  setSmtpPassword(pw: string) {
    this.notification.password = pw;
  }
  @Mutation
  setSmtpUsername(name: string) {
    this.notification.username = name;
  }
  @Mutation
  setSmtpRecipients(rec: string[]) {
    this.notification.recipient = rec;
  }
  @Mutation
  setPort(port: number) {
    this.general.port = port;
  }

  get theme() {
    return this.gui.theme;
  }
  get user() {
    return this.userData;
  }
  get language() {
    return this.gui.language;
  }
  get showSnackbar() {
    return this.gui.showSnackbar;
  }
  get automaticUpdates() {
    return this.general.automaticUpdates;
  }
  get enableRegister() {
    return this.general.enableRegister;
  }
  get updateCheckInterval() {
    return this.general.updateCheckInterval;
  }
  get username() {
    return this.userData.username;
  }
  get firstName() {
    return this.userData.firstName;
  }
  get lastName() {
    return this.userData.lastName;
  }
  get email() {
    return this.userData.email;
  }
  get defaultUnlockTime() {
    return this.backup.defaultUnlockTime;
  }
  get defaultMaxBackupsPerRepo() {
    return this.backup.defaultMaxBackupsPerRepo;
  }
  get defaultEmailNotification() {
    return this.backup.defaultEmailNotification;
  }
  get sendEmails() {
    return this.notification.sendEmails;
  }
  get smtpHostname() {
    return this.notification.hostname;
  }
  get smtpPort() {
    return this.notification.port;
  }
  get reportLanguage() {
    return this.notification.reportLanguage;
  }
  get smtpFrom() {
    return this.notification.from;
  }
  get smtpPassword() {
    return this.notification.password;
  }
  get smtpUsername() {
    return this.notification.username;
  }
  get smtpRecipients() {
    return this.notification.recipient;
  }

  get fullUserName() {
    return (
      `${this.userData.firstName} ${this.userData.lastName}`.trim() ||
      this.userData.username
    );
  }
  get port() {
    return this.general.port;
  }
}

const saveSettingsImmediately = async (notInitialUserCreation = true) => {
  const response1 = await api.saveSettings(
    authModule.userId,
    {
      darktheme: settingsModule.theme.dark,
      language: settingsModule.language,
      sendEmails: settingsModule.notification.sendEmails,
      showSnackbar: settingsModule.gui.showSnackbar,
      themeAccent: settingsModule.theme.accent,
      themePrimary: settingsModule.theme.primary,
      themeSecondary: settingsModule.theme.secondary,
      reportLanguage: settingsModule.notification.reportLanguage,
      smtpFrom: settingsModule.notification.from,
      smtpHostname: settingsModule.notification.hostname,
      smtpPassword: settingsModule.notification.password,
      smtpPort: settingsModule.notification.port,
      smtpUsername: settingsModule.notification.username,
      smtpTo: settingsModule.notification.recipient.join(";"),
      defaultEmailNotification: settingsModule.backup.defaultEmailNotification,
      defaultMaxBackupsPerRepo: settingsModule.backup.defaultMaxBackupsPerRepo,
      defaultUnlockTime: settingsModule.backup.defaultUnlockTime
    },
    notInitialUserCreation ? "put" : "post"
  );

  let response3:
    | IServerResponse<IUpdateGlobalSettingsResponseSuccess>
    | undefined;
  let response2: IServerResponse<IUpdateUserResponseSuccess> | undefined;
  // TODO: add all keys
  if (notInitialUserCreation) {
    response2 = await api.saveUserSettings(authModule.userId, {
      email: settingsModule.userData.email,
      firstName: settingsModule.userData.firstName,
      lastName: settingsModule.userData.lastName,
      // password: "asdf", // TODO
      username: settingsModule.userData.username
    });
    response3 = await api.updateGlobalSettings(authModule.userId, {
      logfileSize: 500, // TODO: add settings
      automaticUpdates: settingsModule.general.automaticUpdates,
      enableRegister: settingsModule.general.enableRegister,
      updateCheckInterval: settingsModule.general.updateCheckInterval,
      port: settingsModule.general.port
    });
  }
  if (response1.payload) {
    settingsModule.setShouldUpdate(true);
    settingsModule.setSettings({
      settings: response1.payload,
      user: response2 && response2.payload,
      global: response3 && response3.payload
    });
    requestAnimationFrame(() => {
      settingsModule.setShouldUpdate(false);
    });
  }
};

const saveSettings = saveSettingsImmediately;

