import parser from "cron-parser";
import cronstrue from "cronstrue/i18n";
import { settingsModule } from "@/store/modules";
import {
  distanceInWordsStrict,
  distanceInWords,
  distanceInWordsToNow
} from "date-fns";

import enLocale from "date-fns/locale/en";
import deLocale from "date-fns/locale/de";
import { Language } from "@/shared";

// Format:
// *  *  *  *  *  *
// ┬  ┬  ┬  ┬  ┬  ┬
// │  │  │  │  │  │
// │  │  │  │  │  └ day of week (0 - 7) (0 or 7 is Sun)
// │  │  │  │  └─── month (1 - 12)
// │  │  │  └────── day of month (1 - 31)
// │  │  └───────── hour (0 - 23)
// │  └──────────── minute (0 - 59)
// └─────────────── second (0 - 59, optional)

export const defaultRepeatPattern = "0 0 0 * * MON"; // Every monday at 10:00 AM

export const cleanCronExpression = (expr: string | undefined) =>
  (expr || defaultRepeatPattern)
    .split(/\s/g)
    .map(s => s.trim())
    .filter(s => s && s.length)
    .join(" ");

export const nextCronExecutionDates = (
  expr: string | undefined,
  startDate: Date,
  num: number
) => {
  // return [] as any[];
  const interval = parser.parseExpression(cleanCronExpression(expr), {
    currentDate:
      startDate.getTime() > new Date().getTime() ? startDate : new Date()
  });
  return new Array(num).fill(null).map(() => interval.next().toDate());
};

export const cronExecutionDatesBetween = (
  expr: string | undefined,
  startDate: string,
  endDate: string,
  maxDates = 50
): Date[] => {
  try {
    // options.startDate is only used for reverse iteration
    const interval = parser.parseExpression(cleanCronExpression(expr), {
      currentDate: startDate,
      endDate
    });
    let i = 0;
    const dates = [];
    while (i++ < maxDates && interval.hasNext()) {
      dates.push(interval.next().toDate());
    }
    return dates;
  } catch (error) {
    console.log(error);
    return [];
  }
};

export const nextCronExecutionDate = (
  expr: string | undefined,
  startDate: Date
) => {
  const interval = parser.parseExpression(cleanCronExpression(expr), {
    currentDate:
      startDate.getTime() > new Date().getTime() ? startDate : new Date()
  });
  return interval.next().toDate();
};

export const datesInDistanceInWords = (rawDates: Date[]) =>
  rawDates.map(d =>
    distanceInWordsStrict(d, new Date(), {
      locale: settingsModule.language === Language.de ? deLocale : enLocale
    }).trim()
  );

export const relativeDate = (date: Date) =>
  distanceInWordsToNow(date, {
    locale: settingsModule.language === Language.de ? deLocale : enLocale,
    addSuffix: true
  });

export const humanReadableExpression = (
  expr: string | undefined
): string | false => {
  try {
    return cronstrue.toString(cleanCronExpression(expr), {
      use24HourTimeFormat: settingsModule.language === Language.de,
      locale: settingsModule.language
    });
  } catch (error) {
    return false;
  }
};
