import { Vue, Component, Watch } from "vue-property-decorator";
import { settingsModule } from "@/store/modules";

@Component
export class ThemeUpdaterMixin extends Vue {
  @Watch("currentTheme", { immediate: true, deep: true })
  updateVuetifyTheme() {
    this.$vuetify.theme.primary = this.currentTheme.primary;
    this.$vuetify.theme.secondary = this.currentTheme.secondary;
    this.$vuetify.theme.accent = this.currentTheme.accent;
  }

  get currentTheme() {
    return settingsModule.theme;
  }
}
