import { IServerResponse } from "@/shared";
import { transformResponse } from ".";
import { axiosInstance } from "./axios";
import { socket } from "./socket-io";
import { settingsModule } from "../modules";

export const ping = async () => {
  const response = await axiosInstance.get<{}>("/system/ping");
  return transformResponse(response);
};

export const updateService = async () => {
  const response = await axiosInstance.post<IServerResponse<{}>>(
    "/system/update"
  );
  return transformResponse(response);
};

export const getServiceUpdateAvailable = async () => {
  // TODO: use api
  return {
    available: Math.random() > 0.5
  };
};

socket.on("updateAvailable", () => {
  settingsModule.setUpdateAvailable(true);
});

socket.on("updateComplete", () => {
  // authModule.setLoaded(true);
  // authModule.setServiceUpdating({ updating: false });
  setTimeout(() => {
    if ("serviceWorker" in navigator) {
      navigator.serviceWorker.getRegistrations().then(function(registrations) {
        for (const registration of registrations) {
          registration.unregister();
        }
      });
    }
    if ("caches" in window) {
      caches.keys().then(cacheKeys => cacheKeys.map(caches.delete));
    }
    window.location.reload();
  }, 10000);
});
