


































































































































































import { Vue, Component, Prop, Mixins, Watch } from "vue-property-decorator";
import {
  repositoriesModule,
  jobsModule,
  notificationModule,
  authModule
} from "@/store/modules";
import TheSnackbar from "./TheSnackbar.vue";

@Component({
  components: {
    TheSnackbar
  }
})
export default class TheHeaderComponent extends Mixins() {
  dialog = false;
  drawer: null | boolean = null;
  notificationDrawer = false;
  searchInput = "";
  sidebarModels = [];

  async clearNotifications() {
    await notificationModule.clear();
  }

  async performLogout() {
    await authModule.logout();
    this.$router.push("/login");
  }

  startSearch() {
    this.$router.push({
      path: "/search",
      query: { query: this.searchInput }
    });
  }
  unseenNotificationsDirty = false;

  get layout() {
    return this.$route.meta.layout || "default";
  }
  get isServerReachable() {
    return authModule.isServerReachable;
  }
  get recentlyUsedRepositories() {
    return repositoriesModule.recentlyUsed;
  }
  get recentlyUsedJobs() {
    return jobsModule.recentlyUsed;
  }
  get notificationHistory() {
    return notificationModule.notificationHistory;
  }

  @Watch("notificationHistory")
  setDirty() {
    this.unseenNotificationsDirty = !this.notificationDrawer;
  }

  @Watch("notificationDrawer")
  setNotDirty() {
    this.unseenNotificationsDirty = false;
  }

  get appLoaded() {
    return authModule.isLoaded;
  }

  get items() {
    return [
      { heading: this.$t("navigation.general") },
      { icon: "mdi-home", text: this.$t("navigation.home"), to: "/" },
      {
        icon: "mdi-history",
        text: this.$t("navigation.events"),
        to: "/events"
      },
      {
        icon: "mdi-chevron-up",
        "icon-alt": "mdi-chevron-down",
        text: this.$t("navigation.repositories"),
        group: "/repositories",
        children: [
          {
            icon: "mdi-nas",
            text: this.$t("navigation.all-repositories"),
            to: "/repositories",
            exact: true
          },
          {
            icon: "mdi-plus",
            text: this.$t("navigation.create-repository"),
            to: "/repositories/new"
          },
          ...this.recentlyUsedRepositories.map(repo => ({
            text: repo.name,
            to: `/repositories/${repo.id}`,
            // action: `/repositories/${repo.id}/logs`
            menu: [
              {
                title: this.$t("repository.view-logs"),
                to: `/repositories/${repo.id}/logs`
              },
              {
                title: this.$t("repository.view-snapshots"),
                to: `/repositories/${repo.id}/snapshots`
              },
              {
                title: this.$t("repository.edit-repository"),
                to: `/repositories/${repo.id}/edit`
              },
              {
                title: this.$t("repository.create-job"),
                to: `/jobs/new?repository=${repo.id}`
              }
            ]
          }))
        ]
      },
      {
        icon: "mdi-chevron-up",
        "icon-alt": "mdi-chevron-down",
        text: this.$t("navigation.jobs"),
        group: "/jobs",
        children: [
          {
            icon: "mdi-calendar-check",
            text: this.$t("navigation.all-jobs"),
            to: "/jobs",
            exact: true
          },
          {
            icon: "mdi-plus",
            text: this.$t("navigation.create-job"),
            to: "/jobs/new"
          },
          ...this.recentlyUsedJobs.map(job => ({
            text: job.name,
            to: `/jobs/${job.id}`,
            // action: `/jobs/${job.id}/logs`
            menu: [
              {
                title: this.$t("job.view-logs"),
                to: `/jobs/${job.id}/logs`
              },
              {
                title: this.$t("job.edit-job"),
                to: `/jobs/${job.id}/edit`
              },
              {
                title: this.$t("job.view-repository"),
                to: `/repositories/${job.repoId}`
              }
            ]
          }))
        ]
      },
      { heading: this.$t("navigation.other") },
      {
        icon: "mdi-settings",
        text: this.$t("navigation.settings"),
        to: "/settings"
      },
      { icon: "mdi-help", text: this.$t("navigation.help"), to: "/help" },
      {
        icon: "mdi-account-key",
        text: this.$t("navigation.admin"),
        to: "/admin"
      }
    ];
  }
}
