import store from ".";
import {
  authModule,
  repositoriesModule,
  jobsModule,
  settingsModule,
  adminModule
} from "./modules";

// fetch jobs, settings,clients and repos when user is logged in
store.watch(
  () => ({
    isLoggedIn: authModule.isLoggedIn
  }),
  async ({ isLoggedIn }) => {
    if (isLoggedIn) {
      await Promise.all([
        repositoriesModule.fetchAllRepositories(),
        jobsModule.fetchAllJobs(),
        settingsModule.fetchAllSettings()
      ]);
    }
  },
  { immediate: true }
);
store.watch(
  () => ({
    adminIsLoggedIn: authModule.adminIsLoggedIn
  }),
  async ({ adminIsLoggedIn }) => {
    if (adminIsLoggedIn) {
      await Promise.all([adminModule.fetchAllClients()]);
    }
  },
  { immediate: true }
);
// use a global variable as hot reloading won't clear the interval automatically
clearTimeout(window._PING_TIMEOUT);
const ping = () =>
  requestAnimationFrame(async () => {
    await authModule.pingServer();
    let timeout = 10000;
    if (!authModule.isServerReachable) {
      timeout = 2000;
    }
    window._PING_TIMEOUT = setTimeout(ping, timeout);
  });

ping();
