import {
  IGlobalSettingsArgs,
  IGlobalSettingsResponseSuccess,
  IMailArgs,
  IPatchUserArgs,
  IServerResponse,
  IUpdateGlobalSettingsResponseSuccess,
  IUpdateUserResponseSuccess,
  IUpdateUserSettingsArgs,
  IUpdateUserSettingsResponseSuccess,
  IUserResponseSuccess
} from "@/shared";
import { transformResponse } from ".";
import { axiosInstance } from "./axios";

export const getSettings = async (
  userId: number
): Promise<IServerResponse<IUpdateUserSettingsResponseSuccess>> => {
  const response = await axiosInstance.get<
    IServerResponse<IUpdateUserSettingsResponseSuccess>
  >(`/user/${userId}/usersettings`);
  return transformResponse(response);
};

export const getUserSettings = async (
  userId: number
): Promise<IServerResponse<IUserResponseSuccess>> => {
  const response = await axiosInstance.get<
    IServerResponse<IUserResponseSuccess>
  >(`/user/${userId}`);
  return transformResponse(response);
};

export const saveUserSettings = async (
  userId: number,
  settings: IPatchUserArgs
): Promise<IServerResponse<IUpdateUserResponseSuccess>> => {
  const response = await axiosInstance.patch<
    IServerResponse<IUpdateUserResponseSuccess>
  >(`/user/${userId}`, settings);
  return transformResponse(response);
};

export const saveSettings = async (
  userId: number,
  settings: IUpdateUserSettingsArgs,
  method: "put" | "post"
): Promise<IServerResponse<IUpdateUserSettingsResponseSuccess>> => {
  const response = await axiosInstance[method]<
    IServerResponse<IUpdateUserSettingsResponseSuccess>
  >(`/user/${userId}/usersettings`, settings);
  return transformResponse(response);
};

export const updateGlobalSettings = async (
  userId: number,
  settings: IGlobalSettingsArgs
): Promise<IServerResponse<IUpdateGlobalSettingsResponseSuccess>> => {
  const response = await axiosInstance.put<
    IServerResponse<IUpdateGlobalSettingsResponseSuccess>
  >(`/system/${userId}/globalsettings/1`, settings);
  return transformResponse(response);
};

export const getGlobalSettings = async (
  userId: number
): Promise<IServerResponse<IGlobalSettingsResponseSuccess>> => {
  const response = await axiosInstance.get<
    IServerResponse<IGlobalSettingsResponseSuccess>
  >(`/system/${userId}/globalsettings/1`);
  return transformResponse(response);
};

export const testSmtpSettings = async (userId: number, args: IMailArgs) => {
  const response = await axiosInstance.post<IServerResponse<{}>>(
    `/user/${userId}/mail`
  );
  return transformResponse(response);
};
