import {
  EmailNotification,
  IBackupJobResponseSuccess,
  ICreateBackupJobArgs,
  IGetAllBackupJobsResponseSuccess,
  IServerResponse
} from "@/shared";
import { transformResponse } from ".";
import { IJob } from "../modules/JobsModule";
import { axiosInstance } from "./axios";

export const getJobs = async (
  userId: number
): Promise<IServerResponse<IGetAllBackupJobsResponseSuccess>> => {
  const response = await axiosInstance.get<
    IServerResponse<IGetAllBackupJobsResponseSuccess>
  >(`/user/${userId}/backupJob`);
  return transformResponse(response);
};

export const createJob = async (
  userId: number,
  job: ICreateBackupJobArgs
): Promise<IServerResponse<IBackupJobResponseSuccess>> => {
  const response = await axiosInstance.post<
    IServerResponse<IBackupJobResponseSuccess>
  >(`/user/${userId}/backupJob`, job);
  return transformResponse(response);
};

export const updateJob = async (
  userId: number,
  jobId: number,
  job: ICreateBackupJobArgs
): Promise<IServerResponse<IBackupJobResponseSuccess>> => {
  const response = await axiosInstance.put<
    IServerResponse<IBackupJobResponseSuccess>
  >(`/user/${userId}/backupJob/${jobId}`, job);
  return transformResponse(response);
};

export const deleteJob = async (
  userId: number,
  jobId: number
): Promise<IServerResponse<IBackupJobResponseSuccess>> => {
  const response = await axiosInstance.delete(
    `/user/${userId}/backupJob/${jobId}`
  );
  return transformResponse(response);
};

export const executeJob = async (
  userId: number,
  jobId: number
): Promise<IServerResponse<{}>> => {
  const response = await axiosInstance.post<IServerResponse<{}>>(
    `/user/${userId}/backupJob/${jobId}/execute`,
    {}
  );
  return transformResponse(response);
};
