import runtime from "serviceworker-webpack-plugin/lib/runtime";
import { REGISTER_SERVICE_WORKER } from "@/constants";

if (REGISTER_SERVICE_WORKER) {
  (async () => {
    if ("serviceWorker" in navigator) {
      await runtime.register();
      console.debug("service worker registered");
      await navigator.serviceWorker.ready;
      console.debug("service worker ready");
      if (navigator.serviceWorker.controller) {
        navigator.serviceWorker.addEventListener("controllerchange", event => {
          console.debug("controller changed");
        });
      } else {
        // window needs to be reloaded in order to be controlled by a service worker
      }
    }
  })();
}
