socket.on("finishedInstall", function(
  response: string,
  os: string,
  ip: string,
  id: string
) {
  let message;
  if (os === "Windows") {
    if (response.indexOf("error") === -1 && response.indexOf("Error") === -1) {
      message = {
        messages: [
          {
            name: "api.success.client.install",
            type: MessageType.success
          }
        ],
        payload: {}
      };
      setTimeout(function() {
        registerUserOnClient(ip, id);
      }, 5000);
    } else {
      message = {
        messages: [
          {
            name: "api.error.client.install",
            type: MessageType.error
          }
        ],
        payload: {}
      };
    }
    notificationModule.addFromApiResponse(message);
  } else if (os === "Linux") {
    if (response.indexOf("Installation of Backup380 finished") !== -1) {
      message = {
        messages: [
          {
            name: "api.success.client.install",
            type: MessageType.success
          }
        ],
        payload: {}
      };
      setTimeout(function() {
        registerUserOnClient(ip, id);
      }, 5000);
    } else {
      message = {
        messages: [
          {
            name: "api.error.client.install",
            type: MessageType.error
          }
        ],
        payload: {}
      };
    }
    notificationModule.addFromApiResponse(message);
  } else if (os === "MacOS") {
    if (response.indexOf("Installation of Backup380 finished") !== -1) {
      message = {
        messages: [
          {
            name: "api.success.client.install",
            type: MessageType.success
          }
        ],
        payload: {}
      };
      setTimeout(function() {
        registerUserOnClient(ip, id);
        checkInstallStatus(id);
      }, 5000);
    } else {
      message = {
        messages: [
          {
            name: "api.error.client.install",
            type: MessageType.error
          }
        ],
        payload: {}
      };
    }
    notificationModule.addFromApiResponse(message);
  }
});
