export const registerUserOnClient = async (
  clientIp: string,
  clientId: string
): Promise<IServerResponse> => {
  const user: ICreateUserOnClientArgs = {
    email: "default@default.com",
    firstName: "defaultuser",
    lastName: "defaultuser",
    password: "defaultpassword",
    username: "default",
    ip: clientIp
  };
  const response = await axiosInstance.post<
    IServerResponse<IRegisterUserResponseSuccess>
  >(`/admin/client/${clientId}/addUser`, user);

  return transformResponse(response);
};
export const createRepoOnClient = async (
  clientId: string,
  clientIp: string,
  repoArgs: ICreateBackupRepositoryArgs
): Promise<IServerResponse> => {
  const repoForClient: ICreateBackupRepositoryOnClientArgs = {
    id: clientId,
    ip: clientIp,
    repo: repoArgs
  };

  const response = await axiosInstance.post<
    IServerResponse<ICreateBackupRepositoryResponseSuccess>
  >(`/admin/client/${clientId}/repository`, repoForClient);
  return transformResponse(response);
};

