import { transformResponse } from ".";
import {
  IServerResponse,
  ICreateBackupRepositoryArgs,
  ICreateBackupRepositoryResponseSuccess,
  IGetAllBackupRepositoriesResponseSuccess,
  IUpdateBackupRepositoryArgs,
  IUpdateBackupRepositoryResponseSuccess
} from "@/shared";
import { axiosInstance } from "./axios";

export const getRepos = async (
  userId: number
): Promise<IServerResponse<IGetAllBackupRepositoriesResponseSuccess>> => {
  const response = await axiosInstance.get<
    IServerResponse<IGetAllBackupRepositoriesResponseSuccess> // TODO
  >(`/user/${userId}/repository`);
  return transformResponse(response);
};

export const createRepo = async (
  userId: number,
  repo: ICreateBackupRepositoryArgs
): Promise<IServerResponse<ICreateBackupRepositoryResponseSuccess>> => {
  const response = await axiosInstance.post<
    IServerResponse<ICreateBackupRepositoryResponseSuccess>
  >(`/user/${userId}/repository`, repo);
  return transformResponse(response);
};

export const deleteRepo = async (
  userId: number,
  id: number
): Promise<IServerResponse> => {
  const response = await axiosInstance.delete(
    `/user/${userId}/repository/${id}`
  );
  return transformResponse(response);
};

export const updateRepo = async (
  userId: number,
  repoId: number,
  repo: IUpdateBackupRepositoryArgs
): Promise<IServerResponse<IUpdateBackupRepositoryResponseSuccess>> => {
  const response = await axiosInstance.put<
    IServerResponse<IUpdateBackupRepositoryResponseSuccess>
  >(`/user/${userId}/repository/${repoId}`, repo);
  return transformResponse(response);
};
