import store from "@/store";
import { Action, Module, Mutation, VuexModule } from "vuex-module-decorators";
import * as api from "../api";
import {
  IUpdateClientArgs,
  ILoginArgs,
  IAdminClient,
  IInstallOnClientArgs,
  IDeleteOnClientArgs,
  IClient,
  ICreateBackupRepositoryOnClientArgs,
  ICreateBackupJobArgs,
  ICheckInstallStatusReponse,
  ILoginArgsClient
} from "@/shared";
import { IRepository } from "@/store/modules/RepositoriesModule";
import { Overwrite } from "@/shared";
import { IJob, convertFromInternalToApi } from "./JobsModule";

@Module({ dynamic: true, store, name: "admin", namespaced: true })
export default class AdminModule extends VuexModule {
  private clients: IClient[] = [];
  private curToken = "";
  @Action
  async addClient(client: IClient) {
    const resp = await api.addClient({
      name: client.name,
      ip: client.ip,
      os: client.os
    });
    if (resp.payload) {
      const cClient = resp.payload.client;
      this.setClients([
        ...this.allClients,
        {
          ip: cClient.ip,
          name: cClient.name,
          id: cClient.id,
          os: cClient.os
        }
      ]);
    }

    return resp;
  }

  get allClients() {
    return this.clients;
  }

  get curClientToken() {
    return this.curToken;
  }

  get clientbyID() {
    return (id: string | number) => {
      const num = Number(id);
      const client = this.clients.find(c => Number(c.id) === num);
      return client;
    };
  }

  @Mutation
  setClients(clients: IClient[]) {
    this.clients = clients;
  }
  @Action
  async fetchAllClients() {
    const response = await api.getClients();
    if (response.payload) {
      this.setClients(response.payload.clients);
    }
  }

  @Action
  async updateClient(client: IClient) {
    const resp = await api.updateClient(client.id, {
      ip: client.ip,
      name: client.name
    });
    if (resp.payload) {
      this.setClients(
        this.allClients.map(r => (r.id === client.id ? client : r))
      );
      return resp;
    }
  }
  @Action
  async deleteClient(id: number) {
    const resp = await api.deleteClient(id);
    const newClients = this.allClients.filter(r => r.id !== id);
    this.setClients(newClients);
    return resp;
  }
  @Action
  async installBackupOnClient(data: IInstallOnClientArgs) {
    console.log("send install request");
    const client: IClient | undefined = this.clientbyID(data.id);
    if (client) {
      const adminClient: IAdminClient = {
        username: data.username,
        password: data.password,
        ip: client.ip,
        os: client.os
      };
      if (adminClient) {
        const resp = await api.installOnClient(adminClient, client.id);
        return resp;
      }
    }
  }
  @Action
  async deleteBackupOnClient(data: IDeleteOnClientArgs) {
    const client: IClient | undefined = this.clientbyID(data.id);
    if (client) {
      const adminClient: IAdminClient = {
        username: data.username,
        password: data.password,
        ip: client.ip,
        os: client.os
      };
      if (adminClient) {
        const resp = await api.deleteOnClient(adminClient);
        return resp;
      }
    }
  }
  @Action
  async createRepoOnClient(data: IRepositoryWithClientId) {
    const client: IClient | undefined = this.clientbyID(data.clientId);
    if (client) {
      if (data.clientrepo.s3AccessKey) {
        const resp = await api.createRepoOnClient(data.clientId, client.ip, {
          accessKey: data.clientrepo.s3AccessKey,
          secretAccessKey: data.clientrepo.s3SecretKey,
          autoUnlock: data.clientrepo.autoUnlockDelay,
          repoLocation: data.clientrepo.location,
          repoName: data.clientrepo.name,
          repoPassword: data.clientrepo.password,
          repoType: data.clientrepo.type
        });
        return resp;
      } else {
        const resp = await api.createRepoOnClient(data.clientId, client.ip, {
          autoUnlock: data.clientrepo.autoUnlockDelay,
          repoLocation: data.clientrepo.location,
          repoName: data.clientrepo.name,
          repoPassword: data.clientrepo.password,
          repoType: data.clientrepo.type
        });
        return resp;
      }
    }
  }
  @Action
  async createBackupJobOnClient(job: IBackupJobWithClientId) {
    const client: IClient | undefined = this.clientbyID(job.clientId);
    if (client) {
      const resp = await api.createBackupJobOnClient(
        job.clientId,
        client.ip,
        convertFromInternalToApi(job.clientjob)
      );
      return resp;
    }
  }
  @Action
  async getActiveBackupJobs(id: string) {
    const client: IClient | undefined = this.clientbyID(id);
    if (client) {
      const resp = await api.getActiveBackupJobsOnClient(id);
      console.log("response", resp);
      return resp;
    }
  }
  @Action
  async checkInstallStatus(id: string) {
    const client: IClient | undefined = this.clientbyID(id);
    if (client) {
      const resp = await api.checkInstallStatus(id);
      return resp;
    }
  }

  @Action
  async getRepos(id: string) {
    const client: IClient | undefined = this.clientbyID(id);
    if (client) {
      const resp = await api.getReposOnClient(id);
      if(resp.payload) {
        return resp.payload.repo.map(r =>
          ({
            autoUnlockDelay: r.autoUnlock,
            id: r.id,
            location: r.repoLocation,
            name: r.repoName,
            password: r.repoPassword,
            s3AccessKey: r.accessKey,
            s3SecretKey: r.secretAccessKey,
            type: r.repoType
          } as IRepository))
      }
    }
  }

  @Action
  async getClientInfo(id: string ) {
    const client: IClient | undefined = this.clientbyID(id);
    if (client) {
      const resp = await api.getClient(id);
      return resp;
    }
  }

  @Action
  async getClientUser(id: string) {
    const client: IClient | undefined = this.clientbyID(id);
    if (client) {
      const resp = await api.getUserOnClient(id, {
        username: "default",
        password: "defaultpassword"
      });
      if (resp) {
        if (resp.payload) {
          this.curToken = resp.payload.token;
        }
      }
      return resp;
    }
  }
}
export interface IBackupJobWithClientId {
  clientjob: JobOptionalId;
  clientId: string;
}

export interface IRepositoryWithClientId {
  clientrepo: RepositoryOptionalId;
  clientId: string;
}

export type JobOptionalId = Overwrite<
  IJob,
  {
    id?: number;
  }
>;

export type RepositoryOptionalId = Overwrite<
  IRepository,
  {
    id?: number;
  }
>;
