import {
  IServerResponse,
  IRestoreSnapshotArgs,
  ISnapshotDetailsResponse,
  ISnapshotResponse
} from "@/shared";
import { axiosInstance } from "./axios";
import { transformResponse } from ".";

export const restoreSnapshot = async (
  userId: number,
  repoId: number,
  args: IRestoreSnapshotArgs
): Promise<IServerResponse<{}>> => {
  const response = await axiosInstance.post<IServerResponse<{}>>(
    `/user/${userId}/repository/${repoId}/restore`,
    args
  );
  return transformResponse(response);

  // TODO: event for finished restore
  // TODO: event for restore progress
};

export const getSnapshotDetails = async (
  userId: number,
  repoId: number,
  snapshotId: string
): Promise<IServerResponse<ISnapshotDetailsResponse>> => {
  const response = await axiosInstance.get<
    IServerResponse<ISnapshotDetailsResponse>
  >(`/user/${userId}/repository/${repoId}/snapshot/${snapshotId}`);
  return transformResponse(response);
};

export const listSnapshots = async (
  userId: number,
  repoId: number
): Promise<IServerResponse<ISnapshotResponse>> => {
  const response = await axiosInstance.get<IServerResponse<ISnapshotResponse>>(
    `/user/${userId}/repository/${repoId}/snapshot`
  );
  return transformResponse(response);
};
