

































































































import anime from "animejs";
import { Vue, Component, Prop, Watch, Inject } from "vue-property-decorator";

const setFillValue = (el: HTMLElement) => {
  const stroke = el.getAttribute("stroke");
  if (stroke) {
    const match = /rgb\((\d+),\s*(\d+),\s*(\d+)\)/.exec(stroke);
    if (match) {
      return [`rgba(${match[1]},${match[2]},${match[3]}, 0.0)`, stroke];
    }
  }
  return [`rgba(128, 128, 128, 0.0)`, stroke];
};

@Component({})
export default class Logo extends Vue {
  // random id for animejs
  private id = btoa(Math.random().toFixed(20)).replace(/=/g, "");
  private timeline?: anime.AnimeTimelineInstance;
  @Inject("theme")
  private theme!: { isDark: boolean };

  @Prop({
    type: Boolean,
    default: false
  })
  private loader!: boolean;

  mounted() {
    this.$nextTick(() => {
      this.setupAnimation();
      this.startAnimation();
    });
  }

  startAnimation() {
    if (this.timeline) {
      this.timeline.pause();
      this.timeline.seek(0);
      anime.remove(`#${this.id} path`);
      this.timeline = undefined;
      this.$nextTick(() => {
        this.setupAnimation();
        if (this.timeline) {
          this.timeline.play();
        } else {
          console.warn("No timeline");
        }
      });
    } else {
      console.warn("No timeline");
    }
  }

  setupAnimation() {
    this.timeline = anime.timeline(
      this.loader
        ? {
            direction: "alternate",
            loop: true
            // easing: "linear"
          }
        : {
            loop: false
          }
    );

    this.timeline.add({
      targets: `#${this.id} path`,
      strokeDashoffset: [anime.setDashoffset, 0],
      easing: "easeInOutSine",
      duration: 500,
      delay(el, i) {
        return i * 50;
      }
    });

    this.timeline.add({
      targets: `#${this.id} path`,
      easing: "easeInOutSine",
      fill: { value: setFillValue },
      strokeWidth: 0,
      duration: 500,
      offset: "-=300"
    });

    if (!this.loader) {
      this.timeline.add({
        targets: null,
        duration: 1000
      });
    }

    this.timeline.pause();
  }

  get textColor() {
    return this.theme.isDark ? "rgb(226, 226, 226)" : "rgb(54,54,54)";
  }

  @Watch("theme.isDark")
  onThemeBackgroundChange() {
    this.$nextTick(() => {
      this.startAnimation();
    });
  }
}
