import store from "@/store";
import { Action, Module, Mutation, VuexModule } from "vuex-module-decorators";
import * as api from "../api";
import { settingsModule } from ".";

@Module({ dynamic: true, store, name: "auth", namespaced: true })
export default class AuthModule extends VuexModule {
  private token = "";
  private adminToken = "";
  private _username = "";
  private _anyUserExists = true;
  private serverReachable = true;
  private _userId = 0;
  private _loaded = false;
  private _updating = false;

  get isLoaded() {
    // return false;
    return this._loaded;
  }
  get isLoggedIn() {
    return !!(this.token && this.username && this._userId);
  }
  get adminIsLoggedIn() {
    return !!this.adminToken;
  }

  get username() {
    return this._username;
  }
  get anyUserExists() {
    return this._anyUserExists;
  }
  get authHeader() {
    return `Bearer ${this.token}`;
  }
  get isServerReachable() {
    return this.serverReachable;
  }
  get userId() {
    return this._userId;
  }
  get isServiceUpdating() {
    // return true;
    return this._updating;
  }

  @Mutation
  setServiceUpdating({ updating }: { updating: boolean }) {
    this._updating = updating;
  }

  @Mutation
  setServerResponse({
    token,
    username,
    userId
  }: {
    token: string;
    username?: string;
    userId: number;
  }) {
    this.token = token;
    if (username) {
      this._username = username;
    }
    this._userId = userId;
  }

  @Mutation
  setServerResponseAdmin({ token }: { token: string }) {
    this.adminToken = token;
  }

  @Mutation
  deleteAuthData() {
    this.token = this._username = "";
  }
  @Mutation
  setAnyUserExists(exists: boolean) {
    this._anyUserExists = exists;
  }
  @Mutation
  setServerReachable(reachable: boolean) {
    this.serverReachable = reachable;
  }
  @Mutation
  setLoaded(loaded: boolean) {
    this._loaded = loaded;
  }

  @Action({ rawError: true })
  async authenticate({
    password,
    username
  }: {
    password: string;
    username: string;
  }) {
    const data = await api.login({ username, password });
    this.setServerResponse({
      token: data.payload!.token || "",
      username,
      userId: data.payload!.userId
    });
  }
  @Action({ rawError: true })
  async authenticateAdmin({
    password,
    username
  }: {
    password: string;
    username: string;
  }) {
    const data = await api.adminlogin({ username, password });
    this.setServerResponseAdmin({
      token: data.payload!.token || ""
    });
  }
  @Action({})
  async logout() {
    // TOOD: tell the server to invalidate the token
    await wait(500);
    this.deleteAuthData();
    // TODO: also delete jobs, repositories and settings still saved in the store
  }
  @Action({})
  async createUser(args: {
    password: string;
    username: string;
    firstName: string;
    lastName: string;
    email: string;
  }) {
    const registerUserData = await api.createUser(args);
    if (registerUserData.payload) {
      this.setServerResponse({
        token: registerUserData.payload.token,
        userId: registerUserData.payload.user.id
      });
      await settingsModule.saveUserSettings();
      await settingsModule.fetchAllSettings();
      this.setServerResponse({
        token: registerUserData.payload.token,
        userId: registerUserData.payload.user.id,
        username: registerUserData.payload.user.username
      });
    }
    return registerUserData;
  }

  @Action({ rawError: true })
  async checkIfAnyUserExists() {
    const data = await api.anyUserExists();
    this.setAnyUserExists(!!data.payload!.value);
    this.setLoaded(true);
    return data;
  }

  @Action
  async pingServer() {
    try {
      await api.ping();
      this.setServerReachable(true);
    } catch (error) {
      this.setServerReachable(false);
    }
  }
}

const wait = (time: number) =>
  new Promise(resolve => setTimeout(resolve, time));
