import { nextCronExecutionDates } from "@/misc/cron";
import {
  EmailNotification,
  IBackupJob,
  IBackupJobArgs,
  Overwrite
} from "@/shared";
import store from "@/store";
import { Action, Module, Mutation, VuexModule } from "vuex-module-decorators";
import { authModule } from ".";
import * as api from "../api";

export const convertFromApiToInternal = (job: IBackupJob): IJob => ({
  ...job,
  repeatPattern: job.cronInterval,
  backupLocations: job.backupLocations.split(";"),
  startDate: new Date(job.startDate)
});

export const convertFromInternalToApi = (
  job: Overwrite<IJob, { id?: number }>
): IBackupJobArgs => ({
  ...job,
  cronInterval: job.repeatPattern,
  backupLocations: job.backupLocations.join(";"),
  startDate: job.startDate.getTime()
});

@Module({ dynamic: true, store, name: "jobs", namespaced: true })
export default class SettingsModule extends VuexModule {
  private jobs: IJob[] = [];

  @Action
  async fetchAllJobs() {
    const response = await api.getJobs(authModule.userId);
    if (response.payload) {
      this.setJobs(response.payload.jobs.map(convertFromApiToInternal));
    }
  }

  @Action
  async createJob(
    job: Overwrite<
      IJob,
      {
        id?: IJob["id"];
      }
    > // TODO: maybe make more fields optional
  ) {
    const resp = await api.createJob(
      authModule.userId,
      convertFromInternalToApi(job)
    );
    if (resp.payload) {
      this.setJobs([
        ...this.allJobs,
        convertFromApiToInternal(resp.payload.job)
      ]);
      return resp;
    }
  }

  @Action
  async updateJob(job: IJob) {
    const resp = await api.updateJob(
      authModule.userId,
      job.id,
      convertFromInternalToApi(job)
    );
    if (resp.payload) {
      this.setJobs(this.allJobs.map(j => (j.id === job.id ? job : j)));
      return resp;
    }
  }

  @Action
  async deleteJob(id: number) {
    const resp = await api.deleteJob(authModule.userId, id);
    const newJobs = this.allJobs.filter(r => r.id !== id);
    this.setJobs(newJobs);
    return resp;
  }

  @Action({ rawError: true })
  async executeJob(id: number) {
    const resp = await api.executeJob(authModule.userId, id);
    return resp;
  }

  @Mutation
  setJobs(jobs: IJob[]) {
    this.jobs = jobs;
  }

  get recentlyUsed() {
    // return at most 5 jobs (shown in side menu)
    return this.jobs.slice(0, 5);
  }
  get allJobs() {
    return this.jobs;
  }

  get getById() {
    return (id: string | number) => {
      const num = Number(id);
      const job = this.allJobs.find(r => Number(r.id) === num);
      return job;
    };
  }

  get getByRepository() {
    return (repoId: string | number) => {
      const num = Number(repoId);
      return this.allJobs.filter(j => j.repoId === num);
    };
  }

  get orderedJobsWithNextExecutions() {
    return this.allJobs
      .filter(j => j.active)
      .map(j => ({
        ...j,
        executionDates: nextCronExecutionDates(j.repeatPattern, j.startDate, 5)
      }))
      .sort(
        (a, b) => a.executionDates[0].getTime() - b.executionDates[0].getTime()
      );
  }
}

export interface IJob {
  name: string;
  id: number;
  repoId: number;
  maxBackups: number;
  emailNotification: EmailNotification;
  backupLocations: string[];
  repeatPattern: string;
  active: boolean;
  startDate: Date;
}

export interface IJobsState {
  jobs: IJob[];
}
